import query from "../static/query";
import stylizer from "../static/stylizer";
import str2node from "../static/str2node";
import isNode from "../static/isNode";
import isString from "../static/isString";

/**
 * @property canvas
 * @property context
 * @property properties
 * @method prop
 *
 * @param canvas
 * @param properties
 * @return {{readonly canvas: HTMLCanvasElement, computed: {properties_enabled: boolean, context: ImageBitmapRenderingContext | WebGLRenderingContext | WebGL2RenderingContext | CanvasRenderingContext2D | RenderingContext | OffscreenRenderingContext | OffscreenCanvasRenderingContext2D, properties: [], element: *}, prop(*, *=): (*|undefined), readonly context: CanvasRenderingContext2D}|ImageBitmapRenderingContext|WebGLRenderingContext|WebGL2RenderingContext|CanvasRenderingContext2D|RenderingContext|OffscreenRenderingContext|OffscreenCanvasRenderingContext2D|*}
 * @constructor
 */
const Paint = function (canvas, properties) {
    const root = {
        computed: {
            properties_enabled: true,
            properties: [],
            element: canvas,
            context: canvas.getContext('2d'),
        },
        /** @return HTMLCanvasElement */
        get canvas(){
            return root.computed.element;
        },
        /** @return CanvasRenderingContext2D */
        get context() {
            return root.computed.context = root.computed.element.getContext('2d');
        },
        get properties() {
            return root.computed.properties;
        },
        prop(key, value) {
            if (value === undefined) {
                return root.computed.context[key];
            } else if (root.computed.context[key] !== undefined ) {
                return root.computed.context[key] = root.computed.properties[key] = value;
            }
        },
    };

    if (properties) {
        Object.keys(properties).forEach((key) => {
            root.computed.properties[key] = properties[key];
        });
    }

    if (root.computed.properties_enabled) {
        Object.keys(Paint.properties).forEach((key) => {
            root.computed.context[key] = root.computed.properties[key] !== undefined
                ? root.computed.properties[key]
                : root.computed.properties[key] = Paint.properties[key];
        });
    }

    return root;
}

Paint.properties = {
    direction: "ltr",
    fillStyle: "#000000",
    filter: "none",
    font: "10px sans-serif",
    globalAlpha: 1,
    globalCompositeOperation: "source-over",
    imageSmoothingEnabled: true,
    imageSmoothingQuality: "low",
    lineCap: "butt",
    lineDashOffset: 0,
    lineJoin: "miter",
    lineWidth: 1,
    miterLimit: 10,
    shadowBlur: 0,
    shadowColor: "rgba(0, 0, 0, 0)",
    shadowOffsetX: 0,
    shadowOffsetY: 0,
    strokeStyle: "#000000",
    textAlign: "start",
    textBaseline: "alphabetic",
};

Paint.prop = {
    direction: 'direction',
    fillStyle: 'fillStyle',
    filter: 'filter',
    font: 'font',
    globalAlpha: 'globalAlpha',
    globalCompositeOperation: 'globalCompositeOperation',
    imageSmoothingEnabled: 'imageSmoothingEnabled',
    imageSmoothingQuality: 'imageSmoothingQuality',
    lineCap: 'lineCap',
    lineDashOffset: 'lineDashOffset',
    lineJoin: 'lineJoin',
    lineWidth: 'lineWidth',
    miterLimit: 'miterLimit',
    shadowBlur: 'shadowBlur',
    shadowColor: 'shadowColor',
    shadowOffsetX: 'shadowOffsetX',
    shadowOffsetY: 'shadowOffsetY',
    strokeStyle: 'strokeStyle',
    textAlign: 'textAlign',
    textBaseline: 'textBaseline',
};


export default Paint;
